import React from "react";
import {Link} from "react-router-dom";
import Logo404 from "../images/404Error.svg";
import './styles/NotFound404.css';
function NotFound404(){
        return(
            <React.Fragment>
                <div className="container NotFound__Container">
                    <div className="logo">
                    <img className="image404" src={Logo404} alt=""/>
                    </div>
                    <h1 className="NotFound__text">Click here to go to badges</h1>
                    <Link to="/platziBadges/badges" className="btn btn-primary">Badges</Link>
                </div>
            </React.Fragment>
        );
}
export default NotFound404;