import React from 'react';
import './styles/BadgeEdit.css';
import header from '../images/badge-header.svg';
import Badge from "../components/badge";
import BadgeForm from "../components/BadgeForm";
import api from "../api";
import Loader from "../components/Loader";
class BadgeEdit extends React.Component{
    state={ 
        loading:true,
        error:null,
        form:{
        firstName:"",
        lastName:"",
        email:"",
        jobTitle:"",
        twitter:"",
        avatarUrl:""
    }};

    componentDidMount(){
        this.fetchData()
    }

    fetchData = async() =>{
        try{
            const data = await api.badges.read(
                this.props.match.params.badgeId
            );
            this.setState({
                form:data,
                loading:false
            })

        }catch(error){

        }
    }

    handleChange = e=>{
        this.setState({
            form:{...this.state.form,
            [e.target.name]: e.target.value
            },
        })
    }
    handleSubmit = async e =>{
        e.preventDefault();
        try{
            await api.badges.update(this.props.match.params.badgeId,this.state.form);
            this.setState({ loading:true,error:null});
            this.props.history.push('/platziBadges/badges')
        }catch(error){
            this.setState({loading:false, error:error})
        }
    }
    render(){
        if(this.state.loading){
            return <Loader />
        }
        return(
            <div>
               <div className="BadgeEdit__hero">
                   <img className="img-fluid" src={header} alt="logo.svg"></img>
               </div>
               <div className="container">
                   <div className="row badge-mobile">
                       <div className="col-6">
                        <Badge 
                        name={this.state.form.firstName || "Name"} 
                        lastName={this.state.form.lastName || "Last Name"} 
                        twitter={this.state.form.twitter || "example"} 
                        description={this.state.form.jobTitle || "Jobtitle"} 
                        email={this.state.form.email || "email"} 
                        />
                       </div>
                       <div className="col-6">
                       <h1>Edit Attendant</h1>
                        <BadgeForm 
                        onChange={this.handleChange} 
                        onClick={this.handleSubmit} 
                        formValues={this.state.form}
                        error={this.state.error}/>
                       </div>
                   </div>
               </div>
            </div>
        );
    }
}
export default BadgeEdit;