import React from "react";
import './styles/BadgeListItem.css'
import {Link} from "react-router-dom"
import BadgeListItem from "./BadgesListItem";

    function useSearchBadges(badges) {
        const [query, setQuery] = React.useState('');
        const [filteredBadges, setFilteredBadges] = React.useState(badges);
      
        React.useMemo(() => {
          const result = badges.filter(badge => {
            return `${badge.firstName} ${badge.lastName}`
              .toLowerCase()
              .includes(query.toLowerCase());
          });
      
          setFilteredBadges(result);
        }, [badges, query]);
      
        return { query, setQuery, filteredBadges };
      }

function BadgesList(props){
    const badges = props.badges.reverse();
    const {query, setQuery, filteredBadges} = useSearchBadges(badges)
                if(filteredBadges.length === 0){
                    return(
                        <React.Fragment>
                            <div className="form-group">
                                    <label>Filter Badges</label>
                                    <input 
                                    type="text" 
                                    className="form-control"
                                    value={query}
                                    onChange={(e)=>{
                                        setQuery(e.target.value);
                                    }}/>
                                </div>
                            <div className="container">
                                <h3> No badges were found </h3>
                                <Link className="btn btn-primary" to="/badges/new"> Create new Badge</Link>
                            </div>  
                        </React.Fragment>
                    )
                }else{
                    return(
                        <React.Fragment>
                            <div className="form-group">
                                    <label>Filter Badges</label>
                                    <input 
                                    type="text" 
                                    className="form-control"
                                    value={query}
                                    onChange={(e)=>{
                                        
                                        setQuery(e.target.value);
                                    }}/>
                                </div>
                            <ul className="Badges__container">
                        {filteredBadges.map((e) =>(
                                <Link key={e.id}to={`/platziBadges/badge/${e.id}`} className="text-reset text-decoration-none BadgeListItem__container container">
                                    <BadgeListItem badge={e}/>
                                </Link>
                        ))}
                            </ul>
                        </React.Fragment>
                    )
                }
    }

export default BadgesList;