import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Badges from "../pages/Badges";
import BadgeNew from "../pages/BadgeNew";
import BadgeEdit from "../pages/BadgeEdit";
import NotFound from "../pages/NotFound404";
import BadgeDetails from "../pages/BadgeDetailsContainer";
import Layout from "../components/Layout";

function App(){
    return(
        <BrowserRouter>
            <Layout>
                <Switch>
                    <Route exact path="/platziBadges/badges" component={Badges}/>
                    <Route exact path="/platziBadges/badge/new" component={BadgeNew}/>
                    <Route exact path="/platziBadges/badge/:badgeId" component={BadgeDetails}/>
                    <Route exact path="/platziBadges/badge/:badgeId/edit" component={BadgeEdit}/>
                    <Route component={NotFound}/>
                </Switch>
            </Layout>
        </BrowserRouter>
    );
}

export default App;