import React from "react";
import './styles/BadgeListItem.css'
import {FaTwitter} from "react-icons/fa";
import Gravatar from "./Gravatar";


class BadgesListItem extends React.Component{
    render(){
        const twitterURL = "https://twitter.com/"
        // const badge = this.props;
        return(
            <React.Fragment>
                <div>
                    <Gravatar email={this.props.badge.email} className="BadgeListItem__img"/>
                </div>
                <div className="BadgeListItem__info">
                        <h1>{this.props.badge.firstName}<br></br> {this.props.badge.lastName}</h1>
                        <a href={`${twitterURL+this.props.badge.twitter}`}><FaTwitter/> @{this.props.badge.twitter}</a>
                    <p>{this.props.badge.jobTitle}</p>
                </div>
            </React.Fragment>
        )
    }
}
export default BadgesListItem;