import React from "react";
import "./styles/BadgeForm.css";

class BadgeForm extends React.Component{
    // state={
    //     firstName:'',
    //     lastName:'',
    //     email:'',
    //     jobtitle:'programing',
    //     twitter:''
    // };
    // handleChange = e =>{
    //     this.setState({
    //         [e.target.name]: e.target.value,
    //     });
    // };
    handleClick = e =>{
        console.log('Button was clicked');
    };
    handleSubmit = e =>{
        e.preventDefault();
        console.log('Form was submitted');
        console.log(this.state);
    }
    render(){
        return(
            <div>
                <form onSubmit={this.props.onSubmit}>
                    <div className="form-group">
                        <label htmlFor="">First name</label>
                        <input value={this.props.formValues.firstName} onChange={this.props.onChange}type="text" className="form-control" name="firstName"/>
                        <label htmlFor="">Last name</label>
                        <input value={this.props.formValues.lastName} onChange={this.props.onChange}type="text" className="form-control" name="lastName"/>
                        <label htmlFor="">Email</label>
                        <input value={this.props.formValues.email} onChange={this.props.onChange}type="text" className="form-control" name="email"/>
                        <label htmlFor="">Job Title</label>
                        <input value={this.props.formValues.jobTitle} onChange={this.props.onChange}type="text" className="form-control" name="jobTitle"/>
                        <label htmlFor="">Twitter</label>
                        <input value={this.props.formValues.twitter} onChange={this.props.onChange}type="text" className="form-control" name="twitter"/>
                        
                        <button 
                        type="submit" 
                        onClick={this.props.onClick} 
                        className="btn btn-primary">
                            Save
                        </button>
                        {this.props.error && (<p className="text-danger">{this.props.error.message}</p>)}
                            
                    </div>
                </form>
            </div>
        );
    }
}

export default BadgeForm;