import React from "react";
import { Link } from "react-router-dom";
import "./styles/Badges.css";
import confLogo from "../images/badge-header.svg";
import BadgeList from "../components/BadgesList";
import Loader from "../components/Loader";
import api from "../api"

class Badges extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            loading:true,
            error:null,
            // nextPage:1,
            data:[]
            // data:{
            //     results:[]
            // }
        }
    }

    render(){

        if(this.state.loading === true && !this.state.data){
            return(<Loader/>);
        }

        if(this.state.error){
            return `Error:  ${this.state.error}`;
        }

        return(
        <React.Fragment>
            <div className="Badges">
                <div className="Badges__hero">
                    <div className="Badges__container">
                        <img className="Badges_conf-logo" src={confLogo} alt="Logo de la platzi Conf"/>
                    </div>
                </div>
            </div>
            <div className="Badge__container">
                <div className="Badges__buttons">
                    <Link to="/platziBadges/badge/new" className="btn btn-primary">New Badge </Link>       
                </div>
            </div>
                <div className="container">
                    <BadgeList badges={this.state.data}/>
                </div>
                
                {this.state.loading && (
                        <Loader />
                )}
                {!this.state.loading  &&(
                <div className="Badges__buttons">
                    <button onClick={() => this.fetchData()} className="btn btn-primary">Load more</button>
                </div>
                )}
        </React.Fragment>
        );
    } 

    componentDidMount(){
        this.fetchData();
        this.intervalId =setInterval(this.fetchData(),5000);
    }

    fetchData = async () => {
        // const APIURL = `https://rickandmortyapi.com/api/character?page=${this.state.nextPage}`
        try{
            // const response = await fetch(APIURL);
            // const data = await response.json();
            this.setState({
                // data:{
                //     info:data.info,
                //     results: [].concat(
                //         this.state.data.results,
                //         data.results,
                //     )
                // },
                data: await api.badges.list(),
                loading:false,
                // nextPage:this.state.nextPage+1,
                error:false
            });
        }catch(error){
            this.setState({
                loading:false,
                error:error,
            })
        }
    } 

    componentDidUpdate(prevState,prevProps){
    }
    componentWillUnmount(){
        clearInterval(this.intervalId);
    }
}

export default Badges;