import "../pages/styles/BadgeDetails.css";
import React from "react";
import confLogo from "../images/logo_conf.svg";
import Badge from "../components/badge";
import {Link} from "react-router-dom";
import DeleteBadgeModal from "../components/DeleteBadgeModal"
function BadgeDetails(props){
    const badge = props.badge;
    return(
        <React.Fragment>
            <div className="BadgesDetails__hero">
                <div className="container">
                    <div className="row">
                        <div className="col-6">
                            <img className="img-fluid" src={confLogo} alt="logo.svg"></img>
                        </div>
                        <div className="col-6">
                            <h1>{badge.firstName} {badge.lastName}</h1>
                        </div>
                    </div>
                </div>
            </div>
            <div className="container">
                <div className="row">
                    <div className="col">
                        <Badge 
                        name={badge.firstName || "Name"} 
                        lastName={badge.lastName || "Last Name"} 
                        twitter={badge.twitter || "example"} 
                        description={badge.jobTitle || "Jobtitle"} 
                        email={badge.email || "email"} 
                        ></Badge>
                    </div>
                    <div className="col">
                        <h2>Actions:</h2>
                        <Link className="btn btn-primary mb-4" to={`/platziBadges/badge/${badge.badgeId}/edit`}>Edit</Link>
                        <br/>
                        <button  onClick={props.onOpenModal} className="btn btn-danger">Delete</button>
                        <DeleteBadgeModal isOpen={props.modalIsOpen} onClose={props.onCloseModal} onDelete={props.deleteBadge}/>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}
export default BadgeDetails;